<template>
  <d2-container>
    <template slot="header">
      <instence-select :isupf="false" @changeInstence="changeInstance"></instence-select>
    </template>

    <el-card class="box-card">
      <el-form label-position="left" class="demo-table-expand" size="mini">
        <el-form-item label="N3接口接收GTP包数："><span>{{ serverData.upf_n3_inc_pkt }}</span></el-form-item>
        <el-form-item label="N3接口发送GTP包数："><span>{{ serverData.upf_n3_og_pkt }}</span></el-form-item>
        <el-form-item label="N3接口接收GTP包字节数"><span>{{ serverData.upf_n3_inc_oct }}</span></el-form-item>
        <el-form-item label="N3接口发送GTP包字节数"><span>{{ serverData.upf_n3_og_oct }}</span></el-form-item>
        <el-form-item label="N3接口接收错误GTP包数"><span>{{ serverData.upf_n3_disc_pkt }}</span></el-form-item>
      </el-form>
    </el-card>

    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane v-for="(item, index) in tabPaneList" :key="index" :label="item.tabLabel" :name="item.tabLabel">
        <el-table :data="serverData.n3IncPktDnnList" style="width: 100%">
          <!-- <el-table-column prop="id" label="ID" width="180">
          </el-table-column> -->
          <el-table-column prop="dnn_name" label="DN_Name" width="180">
          </el-table-column>
          <el-table-column prop="n3_inc_pkt" label="N3接口接收GTP包数">
          </el-table-column>
          <el-table-column prop="n3_og_pkt" label="N3接口发送GTP包数">
          </el-table-column>
          <el-table-column prop="n3_inc_oct" label="N3接口接收GTP包字节数">
          </el-table-column>
          <el-table-column prop="n3_og_oct" label="N3接口发送GTP包字节数">
          </el-table-column>
          <el-table-column prop="n3_disc_pkt" label="N3接口接收错误GTP包数">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import {
  fetchGetPerformN3,
} from "@/api/demo/index";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  components: { instenceSelect },
  name: "performN3",
  data() {
    return {
      activeName: "分DN统计",
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: {},
      tabPaneList: [
        {tabLabel: '分DN统计'}
        // { tabLabel:'N3接口接收GTP包数', dataKey: 'n3IncPktDnnList', columKey: 'n3_inc_pkt' },
        // { tabLabel:'N3接口发送GTP包数', dataKey: 'n3OgPktDnnList', columKey: 'n3_og_pkt' },
        // { tabLabel:'N3接口接收GTP包字节数', dataKey: 'n3IncOctDnnList', columKey: 'n3_inc_oct' },
        // { tabLabel:'N3接口发送GTP包字节数', dataKey: 'n3OgOctDnnList', columKey: 'n3_og_oct' },
        // { tabLabel:'N3接口接收错误GTP包数', dataKey: 'n3DiscPktDnnList', columKey: 'n3_disc_pkt' }
      ]
    };
  },
  mounted() {
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = {};
      this.instanceValue = val

      // 获取告警数据
      this.getData();
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetPerformN3({
        request_type: "n3 perform",
        instance_id: this.instanceValue,
        sequence_no: 3355,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
            // 拼接组装表格数据
            if (this.serverData.n3IncPktDnnList) {
              this.serverData.n3IncPktDnnList.forEach((element, index) => {
                element.n3_og_pkt = this.serverData.n3OgPktDnnList[index].n3_og_pkt
                element.n3_og_pkt = this.serverData.n3IncOctDnnList[index].n3_inc_oct
                element.n3_og_pkt = this.serverData.n3OgOctDnnList[index].n3_og_oct
                element.n3_og_pkt = this.serverData.n3DiscPktDnnList[index].n3_disc_pkt
              })
            }
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

